<template>
	<view class="classificationAnkings">
		<!-- 返回首页-->
		<view class="header-back" @click="backHome">
			<i class="iconfont icon-xiangzuojiantou"></i>
		</view>
		<!-- 头部标题 -->
		<div class="header">
			<view class="title">
				<i class="iconfont icon-icon_maisui_zuo"></i>
				宜家排行榜
				<i class="iconfont icon-icon_maisui_you"></i>
			</view>
			<view class="des">
				排名依据7日销量计算 · 每日更新
			</view>
			<view class="title-list">
				<view class="title-item" v-for="(item,index) in titleList" :key="index" :class="{'active':titleActive == index}" @click="choseType(index)">
					{{item}}
				</view>
			</view>
		</div>
	</view>
</template>

<script>
	import {
		getListDetailst
	} from "@/api/rankingList"
	export default {
		data() {
			return {
				titleList: [], // 分类标题名
				titleActive:0,// 分类选中下标

			}
		},
		methods: {
			//返回首页
			backHome() {
				uni.navigateBack(1)
			},
			// 获取排行榜那个分类别表数据
			async getListDetailstFun() {
				try {
					let res = await getListDetailst({
						title: this.$route.query.title,
					})
					this.titleList = res.data.list
					console.log(this.titleList);

				} catch (err) {
					uni.showModal({
						title: `失败222`,
					})
				}
			},
			// 选择分类
			choseType(index){
				this.titleActive = index
			}

		},
		onLoad() {
			this.getListDetailstFun()
		},
		mounted() {


		}
	}
</script>

<style lang="scss">
	.classificationAnkings{
		position: relative;
	}
	.header-back {
		position: fixed;
		top: 0;
		left: 0;
		padding: 10px 0 10px 10px;

		.iconfont {
			font-size: 20px;
			font-weight: bolder;
			color: #fff;
		}
	}

	.header {
		width: 100%;
		height: 25vh;
		background-color: #ad4646;
		color: #fff;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
    
		.title {
			font-size: 28px;

			.iconfont {
				font-size: 28px;
			}
		}

		.des {
			font-size: 12px;
			opacity: .7;
			margin-top: 10px;
		}

		.title-list {
			box-sizing: border-box;
			padding: 0 20px;
			min-width: 100vw;
			display: flex;
			justify-content: flex-start;
			overflow: auto;
			position:  absolute;
			bottom: 20px;
			
		}

		.title-item {
			flex-shrink: 0;
			padding: 12px 16px;
			font-size: 12px;
			background-color: #0000002a;
			border-radius: 999px;
			margin: 0 6px;
		}
		.title-item.active{
			background-color: #fff;
			color: #111;
		}

	}
</style>